.header-welcome {
    min-height: 50px;
    display: flex;
    align-items: center;
    justify-content: start;
    padding: 2% 5% ;
    gap: 1rem;
}

.container-welcome{
    background-image: url("/public/images/fantastic-blue-sky.jpg");
    background-size: cover;
    height: 100%;
    width: 100%;
    overflow: scroll;
    background-position: center;
}

.logo-welcome{
    height: 80px;
    width: 80px;
}

.header-title{
    font-weight: bold;
    font-size: x-large;

}


.body-welcome{
    display: flex;
    gap: 15px;
    margin: 5px;
}

.text-contents{
    font-size: 1rem;
    letter-spacing: 3px;
    padding: 0 15px;
    background-color: black;
    color: white;
    text-transform: uppercase;
}

.text-boas-vindas {
    font-size: 1rem;
    margin: 8px 0;
    letter-spacing: 3px;
    padding: 0 15px;
    position: relative; 
}

.text-boas-vindas::before {
    content: '';
    display: block;
    width: 5px;
    height: 100%; 
    background-color: aqua;
    position: absolute;
    top: 0px; 
    left: 0;
}

