.option-welcome{
    cursor: pointer;
}

.option-welcome:hover{
    background-color: rgb(209, 209, 209);
}

.body-content{
    font-size: 1rem;
}

.body-title{
    font-weight: bold;
    font-size: large;
}

.contact-option{
    margin: 15px 0;
}

.contact-option > a{
    display: flex;
    align-items: center;
    gap: 8px;
}

.logo-contact{
    width: 30px;
    height: 30px;;
}

@media (max-width: 1024px) {
    
    .logo-welcome{
        height: 40px;
        width: 40px;
    }

    .header-title{
        font-weight: bold;
        font-size: large;

    }

}
  
  
@media (max-width: 768px) {
   

    .body-welcome{
        flex-wrap: wrap;
    }

    .body-welcome > *{
      width: 100%;
    }

}