.init-menu {
  position: absolute; 
  z-index: 2000;
  top: -949%; 
  left: 3px;
  width: auto;   
  display: grid; 
  grid-template-columns: 13% 1fr; 
  align-items: start;
  background-color: #c0c0c0; 
  padding: 2px;
}

.title-menu-init {
  z-index: 2000;
  height: 98.5%;
  width: 100%;
  background: linear-gradient(90deg, navy, #1084d0);
  padding: 3px 2px 3px 3px;
  position: relative;
}


.title-menu-init > * {
  writing-mode: sideways-lr;
  -webkit-writing-mode: sideways-lr;
  -ms-writing-mode: sideways-lr;
  text-align: left;
}


.init-menu ul {
  list-style: none; 
  padding: 0; 
  margin: 0;   
}

.init-menu li {
  padding: 10px;
  cursor: pointer; 
}

.init-menu li:hover {
  background-color: navy;
  color: white;
}

.container-opt-menu-init{
display: flex;
align-items: center;
gap: 5px;
}

.icon-opt-menu-init{
height: 25px;
width: auto;
}