.window {
  position: fixed;  
  z-index: 1000; 
  opacity: 1;
  overflow: hidden; 
  position: relative;
  transition: width 0.3s ease, height 0.3s ease;
}

.window.maximized {
  transform: translate(0px, 0px) !important;
  left: 0 !important;
  top: 0 !important;
}

.window.minimized {
  animation: minimize 0.3s forwards; 
}

@keyframes minimize {
  0% {
    opacity: 1;
  }
  100% {
    width: 0%;
    height: 0%;
    opacity: 0; 
    left: 50%; 
    top: 100%;  
  }
}

.window.minimized.focused {
  animation: restore 0.3s forwards;
}

@keyframes restore {
  0% {
    width: 0%;
    height: 0%;
    opacity: 0;
    left: 50%; 
    top: 100%;  
  }
  100% {

    opacity: 1;
  }
}

.header-window{
  display: flex;
  align-items: center;
}

.icon-app-window{
  height: 15px !important;
  margin-right: 5px;
}

.window-body{
  padding: 0 !important;
  margin: 0 !important;
  width: 100%;
  height: calc(100% - 21px);;
}