.navbar {
  display: grid; 
  grid-template-columns: 92% 8%; 
  height: 28px; 
  background-color: #c0c0c0; 
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  padding: 5px 0;
}
  
.navbar-section, .navbar-section > * {
  display: flex; 
  align-items: center; 
  justify-content: start; 
  font-weight: bold; 
  overflow: hidden;
}

.btn-init{
  margin: 5px !important;
  padding: 5px;
  height: 100%;
}

.btn-init > * {
  padding:  0px 5px;
}

.logo-init{
  width: 20px;
  height: 20px;
}

.end-task-bar{
  display: flex;
  justify-content: flex-end;

}

.end-task-bar button {
  height: 100%;
  margin: 0 5px;
}

.container-icon-notify{
  height: 100% ;
  display: flex;
  align-items: center;
}

.icon-task-bar{
  height: 70% !important;
  padding: 0 5px;
}

@media (max-width: 1700px) {
  .navbar {
    grid-template-columns: 75% 25%;
  }
}


@media (max-width: 768px) {
  .navbar {
    grid-template-columns: 75% 25%;
  }

  .btn-init, .end-task-bar button {
    padding: 3px; 
  }

  .logo-init {
    width: 16px;
    height: 16px; 
  }

  .container-icon-notify{
    display: none;
  }

  .end-task-bar{
    justify-content: end;
  }

  .clock{
    width: 100% !important;
    text-align: center;
  }
}