.container-notepad{
    height: 100%;
    width: 100%;
    overflow: scroll;
}

.container-notepad > .field-row-stacked{
    height: calc(100% - 18px) ;
    width: 100%;
}

.container-notepad > .field-row-stacked > textarea {
    height: 100%  ;
    width: 100%;

}

.container-navbar-window{
    display: flex;
    gap: 5px;
    color: black;
}

.opt-menu-window{
    margin: 0px;
    padding: 3px;
    cursor: pointer;
    min-width: 15px;
    text-align: center;
}

.opt-menu-window:hover{
    background-color: #aaaaaa;
}

.window-firstleter-menu{
    text-decoration: underline;
}

